@import url('https://fonts.googleapis.com/css?family=Roboto');
@font-face {
  font-family: 'sophia';
  src: url('Sophia.woff2') format('woff2'),
       url('Sophia.woff') format('woff');
}
@font-face {
  font-family: 'sophia-right';
  src: url('SophiaRight.woff2') format('woff2'),
       url('SophiaRight.woff') format('woff');
}

body {
  margin: 0;
}

.App {
  position: relative;
  height: 100vh;
  text-align: center;
  padding-top: 15%;
}

.App:before {
  background: url('https://images.unsplash.com/photo-1444394668777-6fa6de8b8356?') center / cover;
  content: '';
  position: absolute;
  left: 0; right: 0; top: 0; bottom: 0;
  z-index: -1;
  opacity: 0.4;
}

.hashtag {
  background: -o-linear-gradient(120deg, rgba(147, 112, 219, .95), rgba(255, 197, 208, .95));
  background: -webkit-linear-gradient(330deg, rgba(147, 112, 219, .95), rgba(255, 197, 208, .95));
  background: -o-linear-gradient(330deg, rgba(147, 112, 219, .95), rgba(255, 197, 208, .95));
  background: linear-gradient(120deg, rgba(147, 112, 219, .95), rgba(255, 197, 208, .95));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 18rem;
  font-family: 'sophia';
}

.tails {
  font-family: 'sophia-right';
}

.d-inline {
  display: inline-block;
  margin: .5rem;
}

.mini {
  display: block;
  font-size: .9rem;
  text-transform: lowercase;
}

.clock {
  font-size: 2.2rem;
  font-family: 'Roboto', sans-serif;
  color: #9370DB;
  margin-top: -1.5rem;
}



